p {
	margin-bottom: 1.5em;
}

dfn, cite, em, i {
	font-style: italic;
}

blockquote {
	margin: 0 1.5em;
}

address {
	margin: 0 0 1.5em;
}

pre {
	background: $color__background-pre;
	font-family: $font__pre;
	@include font-size(0.9375);
	line-height: $font__line-height-pre;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}

code, kbd, tt, var {
	font-family: $font__code;
	@include font-size(0.9375);
}

abbr, acronym {
	border-bottom: 1px dotted $color__border-abbr;
	cursor: help;
}

mark, ins {
	background: $color__background-ins;
	text-decoration: none;
}

big {
	font-size: 125%;
}

.entry-title {
    color: $color__post-title;    
}

.entry-title  a {
    text-decoration: none;
    
}

.post {
    padding: 0 .5em;
}

.sticky {
    background-color: #f2d7ac;
}

.post-thumbnail {
    width: 100%;
    display: flex;
    flex: 0 1 auto;
}

.wp-post-image {
    width: 90vw;
    margin: .5em auto 0 auto;
}