  /* Parallax base styles
  --------------------------------------------- */

  .parallax {
      height: 500px;
      /* fallback for older browsers */
      height: 100vh;
      overflow-x: hidden;
      overflow-y: auto;
      -webkit-perspective: 300px;
      perspective: 300px;
  }

  .parallax__group {
      position: relative;
      height: 500px;
      /* fallback for older browsers */
      height: 100vh;
      -webkit-transform-style: preserve-3d;
      transform-style: preserve-3d;
  }

  .parallax__layer {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
  }

  .parallax__layer--fore {
      -webkit-transform: translateZ(90px) scale(.7);
      transform: translateZ(90px) scale(.7);
      z-index: 1;
  }

  .parallax__layer--base {
      -webkit-transform: translateZ(0);
      transform: translateZ(0);
      z-index: 4;
  }

  .parallax__layer--back {
      -webkit-transform: translateZ(-300px) scale(2);
      transform: translateZ(-300px) scale(2);
      z-index: 3;
  }

  .parallax__layer--deep {
      -webkit-transform: translateZ(-600px) scale(3);
      transform: translateZ(-600px) scale(3);
      z-index: 2;
  }


  

  .parallax__group {
      -webkit-transition: -webkit-transform 0.5s;
      transition: transform 0.75s;
  }

  .parallax {
     font-size: 2em;
     font-family: serif;
     font-style: italic;
     font-weight: 700;
     color: $color__front_page-text;
     
  }

  /* centre the content in the parallax layers */

  .parallax_image{
      text-align: center;
      position: absolute;
      left: 50%;
      top: 50%;
      -webkit-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
  }

  .welcome-title {
      width: 100%;
      position: absolute;
      left: 50%;
      top: 65%;
      -webkit-transform: translate(-40%, -50%);
      transform: translate(-40%, -50%);
  }

  .welcome-title-line-one {
      margin: 0;
  }

  .welcome-title-line-two {
      padding-left: 1em;
  }

  .back-image {
      height: 103vh;
  }
  
  .menu_group {
      position: relative;
      height: 100vh;
      -webkit-transform-style: preserve-3d;
      transform-style: preserve-3d;
  }
  
  .front__menu-images {
      display: flex;
      flex-direction: column;
      
  }
 
  .front__menu-images a {
     display: block;
     margin: auto;      
  }
  
   .front__menu-image {
     max-height: 30vh;
     padding-top: .25em;
  }

  /* style the groups
  --------------------------------------------- */

  #group1 {
      z-index: 5;
      /* slide over group 2 */
  }

  #group1 .parallax__layer--base {
      background: rgb(102, 204, 102);
  }

  #group2 {
      z-index: 3;
      /* slide under groups 1 and 3 */
  }

  #group2 .parallax__layer--back {
      background: rgb(123, 210, 102);
  }

  #group3 {
      z-index: 4;
      /* slide over group 2 and 4 */
  }

  #group3 .parallax__layer--base {
      background: $color__background-body;
  }

  #group4 {
      z-index: 2;
      /* slide under group 3 and 5 */
  }

  #group4 .parallax__layer--deep {
      background: rgb(184, 223, 101);
  }

  #group5 {
      z-index: 3;
      
      /* slide over group 4 and 6 */
  }

  #group5 .parallax__layer--base {
      background: rgb(214, 229, 100);
  }

  #group6 {
      z-index: 2;
      /* slide under group 5 and 7 */
  }

  #group6 .parallax__layer--back {
      background: rgb(245, 235, 100);
  }

  #group7 {
      z-index: 3;
      /* slide over group 7 */
  }

  #group7 .parallax__layer--base {
      background: rgb(255, 241, 100);
  }


  /* misc
  --------------------------------------------- */

  .demo__info {
      position: absolute;
      z-index: 100;
      bottom: 1vh;
      top: auto;
      font-size: 80%;
      text-align: center;
      width: 100%;
  }

  /* Footer
---------------------------------------------*/

  .footer__group {
      position: relative;
      height: 20vh;
      background-color: $color__footer-background;
      -webkit-transform-style: preserve-3d;
      transform-style: preserve-3d;
      z-index: 3;
  }

  .footer {
      display: flex;
      justify-content: space-between;
      padding-bottom: 1em;
  }

  .custom-logo {
      padding: .25em;
      display: block;
      max-height: 15vh;
  }

  .footer-navigation {
      font-size: 1rem;
  }

  .footer-menu {
      display: flex;
      list-style: none;
      margin-left: 0;
  }

  .footer-menu-item a {
      padding-right: .25rem;
      color: $color__footer-menu-text;
      text-decoration: none;
      font-size: .75rem;
      font-weight: 700;
      font-family: sans-serif;
      font-style: normal;
      
      &:hover,
      &:focus{
      color: $color__footer-menu-hover;    
      }
        
  }

  .child-menu {
      padding-left: .5rem;
      list-style: none;
      line-height: .5em;
      margin-left: 0;
  }

  .child-menu-item a {
      color: $color__footer-menu-text;
      text-decoration: none;
      font-size: .5em;
      padding: 0;
      
      &:hover, 
      &:focus{
          color: $color__footer-menu-hover;
      }
  }

